import React from 'react'
import { Link, Navigate  } from "react-router-dom";
import Topbar from './Topbar'

import { useState } from 'react'
import { useAuth } from '../../firebase/AuthContext';
import Error from './Error';

export default function Login() {

    const [errorDesc, setErrorDesc] = useState('')
    const [loading, setLoading] = useState(false)
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')

    const { login, currentUser } = useAuth()

    async function logInHandler() {
        if (email && password) {
            setLoading(true)
            login(email,password)
            .then((response) => console.log(response))
            .catch(e => { setErrorDesc(e.message) })
            .then(setLoading(false))
        }
    }

    return (
        <div>
            {currentUser?<Navigate to="/user"/>:""}
            <Topbar />
            <div className="main-container">
                <div className="transfer-container">
                    <p className="partition-name center">Welcome back!</p>
                    <p className="tc-name">Username:</p>
                    <input value={email} onChange={(e) => setEmail(e.target.value)} className="input" type="text" placeholder="example@gmail.com" />
                    <p className="tc-name">Password:</p>
                    <input value={password} onChange={(e) => setPassword(e.target.value)} className="input" type="password" placeholder="" />

                    <button disabled={loading} onClick={logInHandler} className="button" >Log in</button>

                    <p className="subtext">Don`t have an acoount? <Link className="link" to="/signup">Sign up</Link></p>

                    {errorDesc===''?'':<Error text={errorDesc}/>}
                </div>
            </div>
        </div>
    )
}
