import React from 'react'
import { Link, Navigate } from "react-router-dom";
import Topbar from './Topbar'

import { useState } from 'react'
import { useAuth } from '../../firebase/AuthContext';
import Error from './Error';

export default function Signup() {
    const [loading, setLoading] = useState(false)
    const [email, setEmail] = useState('')
    const [nickname, setNickname] = useState('')
    const [password, setPassword] = useState('')
    const [errorDesc, setErrorDesc] = useState('')

    const { currentUser } = useAuth()
    const { register } = useAuth()

    return (
        <div>
            {currentUser ? <Navigate to="/user" /> : ""}
            <Topbar />
            <div className="main-container">
                <div className="transfer-container" id="auth">

                    <p className="partition-name center">Sign up</p>

                    <p className="tc-name">Your nickname:</p>
                    <input value={nickname} onChange={(e) => setNickname(e.target.value)} className="input" type="text" placeholder="" />
                    <p className="tc-name">Email:</p>
                    <input value={email} onChange={(e) => setEmail(e.target.value)} className="input" type="text" placeholder="example@gmail.com" />
                    <p className="tc-name">Password:</p>
                    <input value={password} onChange={(e) => setPassword(e.target.value)} className="input" type="password" placeholder="" />

                    <button disabled={loading} onClick={async e => {
                        if (email && password && nickname) {

                            e.preventDefault()

                            setLoading(true)

                            register(email, password, nickname)
                                .then(res => {
                                    if (res)
                                        if (res === "user-error") {
                                            setErrorDesc("Firebase: Error (auth/nickname-already-in-use)")
                                        }
                                })
                                .catch(error => { setErrorDesc(error.message) })
                                .finally(() => {
                                    setLoading(false)
                                })
                        } else {
                            setErrorDesc("Please enter valid user data")
                        }
                    }} className="button" >Register</button>

                    <p className="subtext">Already have an acoount? <Link className="link" to="/login">Log in</Link></p>

                    {errorDesc === '' ? '' : <Error text={errorDesc} />}
                </div>
            </div>
        </div>
    )
}
